.sf-lang-selector {
  position: absolute;
  top: 0;
  right: 0;
  display: flex;
  padding: 2px 5px;
  width: max-content;
  border: none;
  border-radius: 3px;
  appearance: none;
  align-items: center;
  line-height: 1;
  font-size: 12px;
  background: #eeeeee;
  cursor: pointer;
  transition: all 0.3s;
  outline: none;
}

.sf-code-block-container .tag {
  padding: 0 !important;
}
