___CSS_LOADER_EXPORT___.push([module.id, `.keyboard-shortcut-dialog {
  max-width: 500px;
}

.keyboard-shortcut-dialog .modal-body {
  max-height: 390px;
  overflow: auto;
}

.keyboard-shortcut-container {
  line-height: 1.5;
  border-radius: 3px;
  padding-top: .5rem;
  padding-bottom: .5rem;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  margin-top: 3px;
  background-color: rgb(248, 248, 248);
}

.keyboard-shortcut-title {
  font-size: 0.9rem;
}

.keyboard-shortcut-container>div:first-child {
  text-align: right;
}

.keyboard-shortcut-container>div:last-child {
  padding-left: 1rem;
  padding-right: 1rem;
}

.keyboard-shortcut {
  display: inline-block;
}

.keyboard-shortcut kbd {
  font: inherit;
  background-color: hsla(0,0%,100%,0.25);
  border-color: rgba(0,0,0,0.1);
  text-transform: uppercase;
  text-align: center;
  border-radius: 3px;
  color: #333333;
  box-sizing: border-box;
  border-style: solid;
  border-width: 1px;
  display: inline-block;
  border-bottom-width: 2px;
  font-size: 12px;
  margin-left: 1px;
  margin-right: 1px;
  margin-bottom: 0px;
  min-width: 12px;
  padding: 1px 3px;
}
