___CSS_LOADER_EXPORT___.push([module.id, `.sf-header-menu {
  display: flex;
  height: 100%;
  width: 140px;
  padding: 6px 8px;
  position: relative;
  align-items: center;
}

.sf-header-menu.header-toggle-disabled::after {
  content: '';
  position: absolute;
  top: 12px;
  right: 12px;
  border-top: 6px solid #e5e6e8;
}

.sf-header-menu.sf-header-popover-showed::after {
  content: '';
  position: absolute;
  top: 7px;
  right: 12px;
  border-top-color: transparent;
  border-bottom-color: #222428;
}

.sf-header-menu .sf-header-toggle {
  flex: 1;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 5px;
  height: 24px;
  cursor: pointer;
}


.sf-header-menu .sf-header-toggle .active {
  font-size: 14px;
}

.sf-header-menu .header-toggle-disabled {
  color: #BDBDBD;
  cursor: unset;
}


.sf-header-menu .sf-header-toggle.sf-header-popover-showed {
  background-color: #f2f2f2;
  border-radius: 2px;
}

.sf-header-menu .sf-header-toggle:not(.header-toggle-disabled):hover {
  background-color: #f2f2f2;
  border-radius: 2px;
}

.sf-header-menu .sf-header-toggle .iconfont {
  color: #555;
  width: 12px;
  height: 24px;
  margin-left: 8px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  font-size: 12px;
  cursor: pointer;
}

.sf-header-menu .sf-header-popover {
  position: absolute;
  top: 32px;
  left: 8px;
  padding: 8px 0;
  width: 100%;
  background-color: #fff;
  border: 1px solid #e5e6e8;
  border-radius: 2px;
  box-shadow: 0 0 10px #ccc;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  z-index: 101;
}

.sf-header-menu .sf-header-popover .sf-dropdown-menu-item {
  font-size: 14px;
  height: auto;
  padding: 9px 16px 9px 28px;
  width: 100%;
  line-height: 1;
}

.sf-header-menu .sf-header-popover .sf-dropdown-menu-item:hover {
  background-color: #F2F2F2;
}

.sf-header-menu .sf-header-popover .sf-dropdown-menu-item .sf-check-mark {
  position: absolute;
  left: 8px;
  transform: scale(.8);
  color: #798d99;
}
