___CSS_LOADER_EXPORT___.push([module.id, `.image-toggle-disabled {
  color: #BDBDBD;
  cursor: unset;
}

.image-popover {
  position: absolute;
  top: 42px;
  left: 8px;
  padding: 8px 0;
  background-color: #fff;
  border: 1px solid #e5e6e8;
  border-radius: 2px;
  box-shadow: 0 0 10px #ccc;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  z-index: 101;
}

.image-popover .image-popover-item {
  margin: 0;
  padding: 9px 16px;
  height: auto;
  width: 100%;
  font-size: 14px;
  line-height: 1;
  white-space: nowrap;
  cursor: pointer;
}

.image-popover .image-popover-item:hover {
  background-color: #4d9ef8;
  color: #fff;
}

.image-popover .image-uploader {
  display: none;
}
