___CSS_LOADER_EXPORT___.push([module.id, `.sf-table-container td {
  position: relative;
}

.sf-table-container .selected-cell {
  caret-color: transparent;
  background-color: #fff4e6;
}

.sf-table-container .selected-cell-top:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  margin: -1px;
  border-top: 1px double #ffa94d;
  z-index: 2;
}

.sf-table-container .selected-cell-bottom:before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: -1px;
  border-bottom: 1px double #ffa94d;
  z-index: 2;
}

.sf-table-container .selected-cell-left:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  margin: -1px;
  border-left: 1px double #ffa94d;
  z-index: 2;
}

.sf-table-container .selected-cell-right:before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  margin: -1px;
  border-right: 1px double #ffa94d;
  z-index: 2;
}

.sf-context-menu {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  /* float: left; */
  z-index: 1000;
  flex-direction: column;
  border: 1px solid rgba(0, 40, 100, 0.12);
  border-radius: 3px;
  background-color: #fff;
}

.sf-context-menu .sf-context-menu-item {
  display: flex;
  clear: both;
  padding: 0.25rem 1.5rem;
  border: none;
  white-space: nowrap;
  color: #212529;
  background-color: #fff;
}

.sf-context-menu .sf-context-menu-item:hover,
.sf-context-menu .sf-context-menu-item:focus {
  color: #16181b;
  text-decoration: none;
  background-color: #f8f9fa;
}

.sf-context-menu .sf-divider {
  width: 100%;
  height: 1px;
  margin: 5px 0;
  color: #eee;
  cursor: default;
}
