___CSS_LOADER_EXPORT___.push([module.id, `.sf-article-info-container {
  flex: 1;
  display: flex;
  flex-direction: column;
  background-color: #f5f5f5;
  user-select: none;
  min-height: 0;
}

.sf-article-info-container .nav {
  padding: 10px 0;
  min-width: 125px;
  height: 36px;
  border-bottom: 1px solid #eee;
  background-color: #fff;
  justify-content: space-around;
  flex-wrap: nowrap;
  flex-shrink: 0;
}

.sf-article-info-container .nav .nav-item {
  padding: 0 0.75rem;
}

.sf-article-info-container .nav .nav-link {
  padding: 0 0.75rem;
  transition: 0.3s color;
  margin-right: 0;
}

.sf-article-info-container .nav-link {
  color: #888;
}

.sf-article-info-container .nav .nav-link.active {
  color: #ff9800;
  border-bottom: 0;
}

.sf-article-info-container .nav .iconfont {
  font-weight: 700;
  font-size: 0.875rem;
}

.sf-article-info-container .sf-article-info-content {
  flex: 1;
  font-size: 0.937rem;
  min-height: 0;
  overflow: auto;
}

.sf-article-info-container .sf-article-info-content:hover {
  overflow: auto;
}
